@use "../../Assets/_variables.scss" as *;

.grid-container--skills {
    display: grid;
    gap: $spacing-medium;
    grid-template-columns: repeat(5, 1fr);
  
    div:nth-child(1) {
      grid-column-start: 1;
      grid-column-end: 6;
      font-size: $font-size-medium;
      text-align: center;
    }
  
    .grid-item {
      text-align: center;
	  font-size: $font-size-medium;
    }

h6 {
margin: 0;
color: $color-secondary;
}

  }