.sidenav {
	height: 100%;
	width: 15rem;
	position: fixed;
	z-index: 1;
	top: 0;
	left: 0;
	background-color: lighten($color-background, 5%);
	overflow-x: hidden;
	padding-top: 20px;



	a {
		display: flex;
		text-decoration: none;
		// color: $color-info;
		padding: $spacing-small;
		font-size: $font-size-small;

		&:hover {
			background-color: $color-background;
		}

		&:active {
			color: $color-primary;
		}
	}


}

.hamburger {
	display: none;
	width: 2rem;
	height: 2rem;
	// display: flex;
	// justify-content: space-around;
	// flex-flow: column nowrap;
	z-index: 100000;
}

.burger {
	display: none;
	width: 2rem;
	height: 0.25rem;
	border-radius: 10px;
	background-color: black;
	transform-origin: 1px;
	transition: all 0.3s linear;
}

/* Mobile View */
@media screen and (min-width: 390px) and (max-width: 844px) {

	.hamburger {
		display: block;
		padding-top: 10px;
		margin-left: 10px;
		z-index: 10000;
	}

	.sidenav {
		display: inline;
		width: 100%;
		height: 5rem;

		a {
			display: inline;
		}

